// Hero Banner
// - - - - - - - - - - - - - - - - - - - - - - - - -
.page-preview {
  @include inView(800ms, 0);
  @include background-img;
  position: relative;    
  background-color: $palette-primary;
  height: rem-calc(720);
  width: 100%;
  padding: rem-calc(50 0);
  color: $palette-secondary;
  display: table; 

  @include breakpoint(medium down) {
    padding: rem-calc(20 0);
    height: rem-calc(600);
  }

  &__inner {
    display: table-cell;
    vertical-align: bottom;
  }

  &__group {
    @include grid-row;
    @include grid-column;
    float: none;
    position: relative;
    z-index: index($z-stack, heroContent);
  }

  &__heading {
    color: $palette-secondary;
    font-size: rem-calc(65);
    margin-bottom: rem-calc(40);

    @include breakpoint(medium down) {
      font-size: $font-size-heading-three;
      margin-bottom: rem-calc(20);
    }
  }

  &__sub-heading {
    color: $palette-secondary;
    margin-bottom: rem-calc(20);
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $global-blend-mode;
    z-index: index($z-stack, heroOverlay);
    filter: grayscale(100%);
  }
}