// Component: Processes
// - - - - - - - - - - - - - - - - - - - - - - - - -

.processes-list {
  width: 100%;
  display: flex;
  flex-direction: row;

  @include breakpoint(medium down) {
    flex-wrap: wrap;
  }

  @include breakpoint(small only) {
    margin-top: rem-calc(80);
  }

  &__item {
    position: relative;
    flex: 1;
    height: 100vh;
    overflow: hidden;

    @include breakpoint(medium down) {
      flex: 1 1 100%;
      height: rem-calc(300);
    }
  }

  &__process {
    background-color: $palette-primary;
    color: $palette-tertiary;
    text-decoration: none;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 300ms $global-transition;
    transition-delay: none;
    padding: rem-calc(10);

    h2 {
      color: currentColor;
      margin-bottom: 0;
    }

    @include hocus {
      outline: none;
      background-color: $palette-accent;
      color: $palette-primary;

      .processes-list__cta {
        transform: translate(-50%, 0);
      }

      .processes-list__icon {
        width: 30px;
      }
    }
  }

  &__title {
    @include inView(800ms, 0);
  }

  // Doubt we will get to 35
  @for $i from 1 to 10 {
    .processes-list__title--delay-#{$i} {
      animation-delay: $i * 250ms;
    }
  }

  &__cta {
    @include btnLink($palette-primary);
    position: absolute;
    bottom: 25px;
    left: 50%;
    transform: translate(-50%, 100px);
    transition: all 300ms $global-transition;
  }

  &__icon {
    @include arrow-right($palette-paragraph);
  }
}