// - - - - - - - - - - - - - - - - - - - - - - - - -
// Component: Divider
// - - - - - - - - - - - - - - - - - - - - - - - - -

.divider {
  @include flex-grid-row;
  margin-bottom: $global-margin;

  &__wrap {
    @include flex-grid-column;
  }

  &__line {
    display: block;
    width: 100%;
    height: 1px;
    background-color: rgba($palette-primary, 0.2);
  }
}