// - - - - - - - - - - - - - - - - - - - - - - - - -
// Object: Lightbox
// - - - - - - - - - - - - - - - - - - - - - - - - -

.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: $palette-tertiary;
  transition: all 400ms $global-transition;

  @include breakpoint(medium down) {
    flex-direction: column;
  }

  &.is-active {
    opacity: 1;
    visibility: visible;
    z-index: index($z-stack, teamPopup);

    .lightbox__image,
    .lightbox__content, {
      transform: translateX(0%);
    }
  }

  &__inner {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    transition: all 400ms $global-transition;

    @include breakpoint(small only) {
      &:first-of-type .lightbox__image {
        margin-top: rem-calc(80);
      }
    }
  }

  &__content,
  &__image { 
    transition: all 0.5s ease-in-out;
    flex: 0 0 50%;
    width: 50%;
    max-width: 50%;
    min-height: 100vh;

    @include breakpoint(medium down) {
      flex: 0 0 100%;
      width: 100%;
      max-width: 100%;
      min-height: auto;
    }
  }

  &__image {
    @include background-img;
    background-color: $palette-secondary;
    transform: translateX(-100%);
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.2) 0%, rgba(255, 255, 255, 0) 100%);
    }

    @include breakpoint(medium down) {
      min-height: rem-calc(300)
    }
  }

  &__content {
    transform: translateX(100%);
    background-color: $palette-tertiary;
    padding-top: rem-calc(30);
    padding-bottom: rem-calc(30);
    padding-right: rem-calc(35);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: auto;

    @include breakpoint(medium down) {
      height: auto;
      padding: rem-calc(20);
      flex: 1;
      overflow: hidden;
    }
  }

  &__info {
    margin-top: rem-calc(200);
    margin-left: rem-calc(76);
    max-width: rem-calc(500);

    @include breakpoint(medium down) {
      margin-top: rem-calc(0);
      margin-left: rem-calc(0);
      margin-bottom: rem-calc(35);
      max-width: none;
    }
  }

  &__pagination {
    width: 100%;
    align-self: flex-end;
    display: flex;
    justify-content: flex-end;

    @include breakpoint(medium down) {
      justify-content: flex-end;
    }
  }

  &__next {
    transition: opacity 0.2s ease-in-out;
    outline: none;
    background: none;
    border: none;
    box-shadow: none;

    &:hover {
      opacity: 0.5;

      .lightbox__icon {
        width: 30px;
      }
    }
  }

  &__cta {
    margin-top: rem-calc(30);
  }

  &__link {
    @include btnLink($palette-primary);
  }

  &__icon {
    @include arrow-right($palette-primary);
  }

  &__close {
    outline: none;
    background: none;
    border: none;
    box-shadow: none;
    position: fixed;
    top: 125px;
    left: 42px;
    text-decoration: none;
    z-index: 999;

    @include breakpoint(medium down) {
      top: 78px;
      left: 35px;
    }

    @include breakpoint(small down) {
      top: 95px;
      left: 20px;
    }
    
    svg {
      transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
    }

    &:hover svg {
      opacity: 0.5;
      transform: translateX(-10px);
    }
  }

  &__sub-heading {
    font-size: rem-calc(10);
    margin-bottom: rem-calc(5);
  }
}


// Body Open Class
.js-lightbox--is-open {
  overflow: hidden;
}