// Component: Case Studies
// - - - - - - - - - - - - - - - - - - - - - - - - -

.case-studies-list {
  width: 100%;
  height: 100vh;
  margin-top: 0 !important;
  overflow-y: hidden;
  overflow-x: auto;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  -webkit-overflow-scrolling: touch;
  background-color: $palette-primary;

  &::-webkit-scrollbar {
    display: none;
  }

  @include breakpoint(medium down) {
    overflow-y: none;
    overflow-x: hidden;
    flex-wrap: wrap;
    height: 100%;
    width: 100% !important;
  }

  &__study {
    width: calc(100% / 4);
    flex: 0 0 auto;
    height: 100vh;
    position: relative;
    overflow: hidden;
    @include inView(800ms, 0);

    @include breakpoint(medium down) {
      width: 100% !important;
      flex: 0 0 100%;
      height: auto;
    }
  }

  // Doubt we will get to 35
  @for $i from 1 to 20 {
    .case-studies-list__study--delay-#{$i} {
      animation-delay: $i * 250ms;
    }
  }
}

.case-studies-case {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;

  @include breakpoint(medium down) {
    height: auto;
    flex-wrap: wrap;
  }

  @include hocus {
    .case-studies-case__content {
      transform: translateY(0%);
    }

    .case-studies-case__logo {
      opacity: 0;

      @include breakpoint(medium down) {
        opacity: 1;
      }
    }

    .case-studies-case__icon {
      width: 30px;
    }

    &:before {
      opacity: 0;

      @include breakpoint(medium down) {
        opacity: 1;
      }
    }
  }
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($palette-primary, 0.8);
    transition: opacity 0.3s linear;
    z-index: index($z-stack, heroContent);
  }

  &__bgImage {
    width: 100%;
    height: 100vh;
    object-fit: cover;
    font-family: 'object-fit: cover;'; // IE11 Polyfill
    position: absolute;
    top: 0;
    left: 0;
    z-index: index($z-stack, heroOverlay);
  }

  &__logo {
    position: relative;
    z-index: index($z-stack, heroContent);
    transition: opacity 0.3s ease;

    @include breakpoint(medium down) {
      padding: rem-calc(132 0);
    }
  }

  &__content {
    background-color: $palette-accent;
    padding: rem-calc(40);
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    transform: translateY(100%);
    transition: transform 0.2s ease;
    z-index: index($z-stack, heroContent);

    @include breakpoint(medium down) {
      transform: none;
      position: static;
      padding: rem-calc(20);
      background-color: $palette-tertiary;
    }
  }

  &__heading {
    @include breakpoint(medium down) {
      display: none;
    }
  }

  &__cta {
    font-size: rem-calc(12);
    font-family: $font-family-secondary;
    color: inherit;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 2px;
    position: relative;
    padding-right: rem-calc(40);
  }

  &__icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: 18px;
    height: 1px;
    transition: all 400ms $global-transition 200ms;
    will-change: width;
    background-color: $palette-primary;

    &:before,
    &:after {
      content: '';
      position: absolute;
      background-color: $palette-primary;
      height: 1px;
      width: 10px;
    }

    &:before {
      top: -3px;
      right: -1px;
      transform: rotate(40deg);
    }

    &:after {
      top: 3px;
      right: -1px;
      transform: rotate(-40deg);
    }
  }
}