// - - - - - - - - - - - - - - - - - - - - - - - - -
// Container: Insights
// - - - - - - - - - - - - - - - - - - - - - - - - -

.insights-banner {
  background-color: $palette-primary;
  color: $palette-tertiary;
  padding-top: rem-calc(136);
  padding-bottom: rem-calc(64);

  @include breakpoint(medium down) {
    padding-top: rem-calc(100);
    padding-bottom: rem-calc(20);
  }

  &--results {
    padding-bottom: rem-calc(30);
  }

  &__row {
    @include flex-grid-row;
  }
}

.insights-banner-top {
  @include flex-grid-row;
  margin-bottom: rem-calc(60);
  align-items: center; 

  @include breakpoint(medium down) {
    margin-bottom: rem-calc(20);
  }

  &__heading {
    @include flex-grid-column(3);

    @include breakpoint(small only) {
      @include flex-grid-column(12);
    }
  }
  
  &__search {
    @include flex-grid-column(9);

    @include breakpoint(small only) {
      @include flex-grid-column(12);
    }
  }
  
  &__field {
    position: relative;
  }
  
  &__title {
    color: $palette-tertiary;
    margin-bottom: 0;
  }

  &__input {
    background: none;
    box-shadow: none;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: 1px solid rgba($palette-tertiary, 0.3);
    color: $palette-tertiary;
    padding: rem-calc(15 35 15 0);

    &.has-error {
      border-bottom: 1px solid $palette-error;

      &:focus {
        border-bottom: 1px solid $palette-error;
      }
    }

    &:focus {
      border-bottom: 1px solid rgba($palette-tertiary, 0.6);
    }
  }

  &__btn {
    @include vertical-center;
    background: none;
    border: 0;
    box-shadow: none;
    right: 0;
    transition: opacity 0.4s linear;
    
    &[disabled] {
      opacity: 0.5;
      cursor: not-allowed;
    }
  }
}

.insights-banner-articles {
  @include grid-row;

  &__article {
    @include grid-column(6);

    &:not(:last-of-type):not(:first-of-type) {
      margin-bottom: rem-calc(40);

      @include breakpoint(small only) {
        @include grid-column(6);
        margin-bottom: rem-calc(5);
      }
    }

    &:nth-of-type(2) {
      @include breakpoint(small only) {
        padding-right: 10px !important;
      }
    }

    &:nth-of-type(3) {
      @include breakpoint(small only) {
        padding-left: 10px !important;
      }
    }

    &:last-of-type {
      @include breakpoint(small only) {
        margin-bottom: rem-calc(0);

        .insights-banner-article__content,
        .insights-banner-article__image, {
          width: 50%;
          float: left;
        }

        .insights-banner-article__image {
          padding-right: rem-calc(10) !important;
        }
  
        .insights-banner-article__content {
          padding-left: rem-calc(10) !important;
          margin-top: 0;
        }
      }
    }

    @include breakpoint(medium down) {
      @include grid-column(12);
      margin-bottom: rem-calc(23);
    }
  }
}

.insights-banner-article {
  text-decoration: none;
  position: relative;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;

  &__image {
    overflow: hidden;

    img {
      transition: all 0.5s ease;
    }
  }

  &__content {
    flex: 1;
    padding-left: rem-calc(30);

    @include breakpoint(small only) {
      padding-left: rem-calc(0);
      width: 100%;
      margin-top: rem-calc(10);
    }
  }

  &__heading {
    color: $palette-tertiary;
    font-size: rem-calc(20);
    line-height: normal;
    transition: color 400ms $global-transition;

    @include breakpoint(medium down) {
      font-size: rem-calc(18);
    }

    @include breakpoint(small only) {
      font-size: rem-calc(14);
    }
  }

  &__meta {
    display: block;
    margin-bottom: rem-calc(10);
    font-size: rem-calc(10);
  }

  &:hover {
    img {
      transform: scale(1.1);
    }

    .insights-banner-article__heading {
      color: $palette-quaternary;
    }
  }

  // First Article
  &--lrg {
    margin-bottom: rem-calc(0);
    overflow: hidden;
    display: block;

    &:hover {
      .insights-banner-article__image {
        transform: scale(1.1);
      }
    }

    .insights-banner-article__image {
      @include background-img;
      position: relative;
      float: none;
      width: 100%;
      height: 450px;
      transition: all 0.5s ease;

      @include breakpoint(medium down) {
        height: 335px;
      }

      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(360deg, rgba(0, 0, 0, 0.5) 0%, rgba(255, 255, 255, 0) 103.11%);
        background-blend-mode: multiply, normal, normal;
        z-index: index($z-stack, heroOverlay);
      }
    }

    .insights-banner-article__content {
      position: absolute;
      bottom: 5px;
      z-index: index($z-stack, heroContent);
      width: 100%;
      padding: rem-calc(0 20);
    }

    .insights-banner-article__heading {
      font-size: rem-calc(36);
      color: $palette-tertiary !important;

      @include breakpoint(medium down) {
        font-size: rem-calc(22);
      }
    }

    .insights-banner-article__meta {
      color: $palette-tertiary;
    }
  }
}

// Blog Category
.insights-banner-cats {
  @include flex-grid-row;
  margin-top: rem-calc(37);
  margin-bottom: rem-calc(37);

  @include breakpoint(medium down) {
    margin-top: rem-calc(10);
    margin-bottom: rem-calc(10);
  }

  &__actionBar {
    @include flex-grid-column;
  }

  &__btn {
    background: none;
    border: 0;
    box-shadow: none;
    padding: 0;
    position: relative;
    display: none;
    width: 100%;
    padding-top: rem-calc(15);
    padding-bottom: rem-calc(15);

    @include hocus {
      outline: none;
    }

    @include breakpoint(medium down) {
      display: table;
    }
  }

  &__title {
    font-size: rem-calc(10);
    display: table-cell;
  }

  &__icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    display: block;

    &:before,
    &:after {
      content: '';
      width: 12px;
      height: 1px;
      background-color: $palette-primary;
      display: block;
    }

    &:after {
      transition: all 150ms ease-in-out;
      transform: rotate(90deg);
      opacity: 1;
      visibility: visible;
    }
  }

  &__group {
    @include flex-grid-column(12);
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include breakpoint(medium down) {
      flex-wrap: wrap;
      flex-direction: column;
      align-items: flex-start;
      position: relative;
      margin-top: rem-calc(7);
      margin-bottom: rem-calc(18);
      padding-bottom: rem-calc(5);
      display: none;

      &.is-active {
        display: flex;
      }

      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #E0E0E0;
      }
    }
  }
  
  &__cat {
    font-family: $font-family-secondary;
    font-size: rem-calc(14);
    text-transform: uppercase;
    letter-spacing: 2px;

    @include breakpoint(medium down) {
      font-size: rem-calc(10);
      color: #666666;
      margin-bottom: rem-calc(16);
    }

    a {
      text-decoration: none;
      transition: opacity 0.2s linear;
      position: relative;

      &:before {
        background: $palette-tertiary;
        bottom: rem-calc(-6);
        content: "";
        height: 1px;
        left: 0;
        position: absolute;
        transform: scaleX(0);
        transform-origin: left;
        transition: transform .3s cubic-bezier(.4,0,.2,1);
        width: 100%;
      }

      &.is-active:before {
        transform: scalex(1);
      }

      &:hover {
        opacity: 0.5;
      }
    }
  }
}

.insights-articles {
  &--search {
    margin-top: rem-calc(60);
  }

  &__row {
    @include flex-grid-row;
    position: relative;

    &.has-error {
      @include flex-grid-column;
      margin-bottom: rem-calc(40);
    }

    &:before {
      content: '';
      background-color: rgba($palette-tertiary, 0.6);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: wait;
      opacity: 0;
      visibility: hidden;
      z-index: -1;
      transition: opacity 200ms $global-transition;
    }

    &.is-searching:before {
      opacity: 1;
      visibility: visible;
      z-index: 2;
      transition: opacity 200ms $global-transition;
    }
  }

  &__search {
    @include flex-grid-column(12);
  }
}
