// Hero Banner
// - - - - - - - - - - - - - - - - - - - - - - - - -
.hero-banner {
  background-size: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
  width: 100%;
  height: rem-calc(650);
  display: table;

  @include breakpoint(medium down) {
    height: 311px;
    padding-top: rem-calc(95);
    background-attachment: unset;
    background-size: cover;
    background-position: center;
  }

  @include breakpoint(small only) {
    margin-top: rem-calc(80);
  }

  // Elements
  &__inner {
    display: table-cell;
    vertical-align: bottom;
  }
  
  &__group {
    position: relative;
    z-index: index($z-stack, heroContent);
    @include flex-grid-row;
    @include flex-grid-column;
  }

  &__heading {
    font-size: rem-calc(65);

    @include breakpoint(medium down) {
      font-size: rem-calc(36);
    }
  }

  &__sub-heading,
  &__heading {
    color: $palette-tertiary;
    flex: 1 1 100%;
  }


  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $global-blend-mode;
    z-index: index($z-stack, heroOverlay);
  }

  // Modifiers
  &--primary {
    background-color: $palette-primary;
    color: $palette-tertiary;
  }

  &--accent {
    background-color: $palette-accent;
    color: $palette-primary;

    .hero-banner__heading,
    .hero-banner__sub-heading {
      color: $palette-primary;
    }
  }

  // Page Overwrites
  .pages-49 & {
    display: none;

    .hero-banner__sub-heading {
      display: none;
    }

    @include breakpoint(medium down) {
      display: table;
    }
  }
}