// - - - - - - - - - - - - - - - - - - - - - - - - -
// Container: Article
// - - - - - - - - - - - - - - - - - - - - - - - - -
$max-width: rem-calc(700);
$bottom-spacing: rem-calc(30);

// - - - - - - - - - - - - - - - - - - - - - - - - -
// Header
// - - - - - - - - - - - - - - - - - - - - - - - - -
.article-header {
  @include inView(800ms, 0);
  background-size: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: $palette-primary;
  color: $palette-tertiary;
  height: rem-calc(650);
  margin-bottom: $bottom-spacing;
  position: relative;
  display: table;
  width: 100%;

  @include breakpoint(medium down) {
    height: 311px;
    padding-top: rem-calc(75);
    background-attachment: unset;
    background-size: cover;
    background-position: center;
  }

  @include breakpoint(small only) {
    margin-top: rem-calc(80);
  }
  
  &__inner {
    display: table-cell;
    vertical-align: bottom;
    width: 100%;
    position: relative;
    z-index: index($z-stack, heroContent);
    text-align: center;
  }

  &__group {
    @include flex-grid-row;
    @include flex-grid-column;
  }

  &--overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $global-blend-mode;
    z-index: index($z-stack, heroOverlay);
  }

  &__heading,
  &__sub-heading {
    color: $palette-tertiary;
    flex: 1 1 100%;
  }

  &__heading {
    font-size: rem-calc(65);

    @include breakpoint(medium down) {
      font-size: rem-calc(45);
    }
  }
}
// - - - - - - - - - - - - - - - - - - - - - - - - -
// Blocks
// - - - - - - - - - - - - - - - - - - - - - - - - -
.article-content-wrap {
  max-width: $max-width;
  margin-left: auto;
  margin-right: auto;

  iframe {
    width: 100%;
    height: rem-calc(393.75);
  }
}

.article-content-copy {
  @include inView(400ms, 125px);
  margin-bottom: $bottom-spacing;

  figure[style*="float:left"] { 
    margin: 0px 10px 10px -150px !important;

    @include breakpoint(medium down) {
      margin: 10px 0px 10px 0px !important;
    }
  }

  figure[style*="float:right"] { 
    margin: 0px -150px 10px 10px !important;

    @include breakpoint(medium down) {
      margin: 10px 0px 10px 0px !important;
    }
  }

  &.is-visible {
    opacity: 1;
    visibility: visible;
    transform: translateY(0px);
  }

  a {
    color: #1C65BA;
  }

  @include breakpoint(small only) {
    padding-left: rem-calc(20);
    padding-right: rem-calc(20);
  }
}

.article-content-code {
  display: block;
  margin-bottom: $bottom-spacing;

  &__snippet {
    display: block;
    max-width: $max-width;
    margin: 0 auto;

    @include breakpoint(small only) {
      padding-left: rem-calc(20);
      padding-right: rem-calc(20);
    }
  }

  &__desc {
    font-size: rem-calc(14);
    
    @include breakpoint(small only) {
      padding-left: rem-calc(20);
      padding-right: rem-calc(20);
    }
  }

  &__code {
    background-color: $palette-secondary;
  }
}

.article-content-image {
  @include flex-grid-row;
  @include inView(800ms, 0);

  &__figure {
    @include flex-grid-column(12);

    &--left-aligned {
      text-align: left;
    }

    &--center-aligned {
      text-align: center;
    }

    &--right-aligned {
      text-align: right;
    }
  }
}

// - - - - - - - - - - - - - - - - - - - - - - - - -
// Comments
// - - - - - - - - - - - - - - - - - - - - - - - - -
.article-comments {
  background-color: $palette-secondary;
  padding: rem-calc(30 0 53);
  border-bottom: 1px solid $palette-tertiary;

  &__wrap {
    max-width: $max-width;
    margin: 0 auto;
  }

  &__comments {
    @include inView(400ms, 130px);
  }

  &__comment {
    background-color: $palette-tertiary;
    padding: rem-calc(30 40);
    margin-bottom: rem-calc(20);
  }

  &__commentor {
    font-family: $font-family-secondary;
    font-size: rem-calc(12);
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-bottom: 0;
  }
}

.article-comments-meta {
  font-family: $font-family-secondary;
  font-size: rem-calc(12);
  text-transform: uppercase;
  letter-spacing: 2px;
  display: flex;
  justify-content: space-between;
  margin-bottom: rem-calc(20);
}

.article-comments-post {
  margin-top: rem-calc(40);
  @include inView(400ms, 130px);

  &__heading {
    margin: rem-calc(28 0 28)
  }

  &__input {
    font-size: rem-calc(16);
    margin-bottom: rem-calc(20);

    &::placeholder {
      font-family: $font-family-normal;
      text-transform: none;
      letter-spacing: 0;
      font-size: rem-calc(16);
    }
  }

  &__comment {
    min-height: rem-calc(160);
  }
}