// Container: Service
// - - - - - - - - - - - - - - - - - - - - - - - - -

.service {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  &__content,
  &__image { 
    flex: 0 0 50%;
    width: 50%;
    max-width: 50%;
    height: 100vh;

    @include breakpoint(medium down) {
      flex: 0 0 100%;
      width: 100%;
      max-width: 100%;
    }
  }

  &__image {
    @include background-img;
    background-color: $palette-secondary;

    @include breakpoint(medium down) {
      height: rem-calc(300)
    }
  }

  &__content {
    padding: rem-calc(30 80);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    @include breakpoint(medium down) {
      height: auto;
      padding: rem-calc(30);
    }
  }

  &__info {
    padding-top: rem-calc(200);
    padding-right: rem-calc(100);

    @include breakpoint(medium down) {
      padding-top: rem-calc(0);
      padding-right: rem-calc(0);
    }
  }

  &__pagination {
    width: 100%;
    align-self: flex-end;
    display: flex;
    justify-content: flex-end;

    @include breakpoint(medium down) {
      justify-content: flex-end;
    }
  }

  &__link {
    @include btnLink($palette-primary);

    @include hocus {
      opacity: 0.5;

      .service__icon {
        width: 30px;
      }
    }
  }

  &__icon {
    @include arrow-right($palette-primary);
  }

  &__back {
    position: fixed;
    top: 115px;
    left: 49px;
    text-decoration: none;
    z-index: 999;
    
    svg {
      transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
    }

    &:hover svg {
      opacity: 0.5;
      transform: translateX(-10px);
    }

    @include breakpoint(medium down) {
      display: none;
    }
  }

  &__sub-heading {
    font-size: rem-calc(10);
    margin-bottom: rem-calc(5);
  }
}