// - - - - - - - - - - - - - - - - - - - - - - - - -
// Components: Case Study Selection
// - - - - - - - - - - - - - - - - - - - - - - - - -

$transition: all 400ms $global-transition;

.case-study-selection {
  @include inView(800ms, 0);
  height: rem-calc(935);
  background-color: $palette-primary;
  color: $palette-tertiary;
  position: relative;

  @include breakpoint(medium down) {
    height: rem-calc(600);
  }

  &__content {
    max-width: $global-width;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: relative;
  }

  &__image {
    @include background-img;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: index($z-stack, heroOverlay);
    opacity: 0;
    visibility: hidden;
    transition: $transition;

    &.is-active {
      opacity: 1;
      visibility: visible;
    }
  }

  &__logos {
    @include inView(800ms, 0);
    position: absolute;
    top: 0;
    right: 0;
    max-width: rem-calc(290);
    display: flex;
    flex-direction: column;
    z-index: index($z-stack, heroContent);

    @include breakpoint(medium down) {
      display: none;
    }
  }

  &__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $palette-primary;
    padding: rem-calc(40);
    width: 100%;
    height: rem-calc(200);
    transition: $transition;
    position: relative;

    img {
      transition: all 400ms $global-transition;
    }

    @include breakpoint(medium down) {
      height: rem-calc(63);
      padding: rem-calc(20);

      img {
        max-width: 50px;
      }
    }

    &:hover {
      background-color: $palette-tertiary;

      img {
        filter: brightness(0%);
      }
    }

    &.is-active {
      background-color: $palette-tertiary;

      svg {
        fill: $palette-primary;
      }

      img {
        filter: brightness(0%);
      }

      &:before {
        content: '';
        @include vertical-center;
        left: -16px;
        border-top: 20px solid transparent;
        border-bottom: 20px solid transparent; 
        border-right: 20px solid $palette-tertiary; 

        @include breakpoint(medium down) {
          left: 50%;
          top: auto;
          bottom: -7px;
          transform: rotate(-90deg) translateY(-16%);
          border-top: 5px solid transparent;
          border-bottom: 5px solid transparent; 
          border-right: 5px solid $palette-tertiary; 
        }
      }
    }
  }

  &__info {
    position: absolute;
    bottom: rem-calc(50);
    left: rem-calc(15);
    z-index: index($z-stack, heroContent);
    max-width: rem-calc(920);
    opacity: 0;
    visibility: hidden;
    transition: $transition;

    img {
      display: none;
    }

    @include breakpoint(medium down) {
      left: rem-calc(20);
      max-width: none;
      padding-right: rem-calc(20);
      bottom: rem-calc(20);

      img {
        display: block;
      }
    }

    &.is-active {
      opacity: 1;
      visibility: visible;
    }
  }

  &__title {
    color: $palette-secondary;
    margin-bottom: rem-calc(50);

    @include breakpoint(medium down) {
      margin-top: rem-calc(20);
      margin-bottom: rem-calc(20);
    }
  }

  &__sub-heading {
    color: $palette-tertiary;

    @include breakpoint(medium down) {
      display: none;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $global-blend-mode;
    z-index: index($z-stack, heroContent);
  }

  &__all {
    @include btnLink($palette-tertiary);
    position: absolute;
    bottom: rem-calc(67);
    right: rem-calc(58);
    z-index: index($z-stack, heroContent);

    @include breakpoint(medium down) {
      display: none;
    }

    @include hocus {
      opacity: 0.5;

      .case-study-selection__icon {
        width: 30px;
      }
    }
  }

  &__icon {
    @include arrow-right($palette-tertiary);
  }

  &__dots {
    @include horizontal-center;
    top: 20px;
    line-height: 0;
    display: none;
    z-index: index($z-stack, heroContent);

    @include breakpoint(medium down) {
      display: block;
    }
  }

  &__dot {
    display: inline-block;
    padding: 0;
    border-radius: 0;
    width: 9px;
    height: 9px;
    margin: 1px;
    background-color: $palette-tertiary;
    border-radius: 50%;
    border-color: transparent;
    opacity: 0.5;
    transition: all 400ms $global-transition;

    &.is-active {
      opacity: 1;
    }

    @include hocus {
      outline: none;
    }
  }
}