// Component: Contact Form
// - - - - - - - - - - - - - - - - - - - - - - - - -

.contact-form {
  padding: rem-calc(60 0 0);
  background-color: $palette-secondary;

  &__row {
    @include flex-grid-row;
    justify-content: center;
    position: relative;
    @include inView(800ms, 0);
  }

  &__intro {
    @include flex-grid-column(6);

    @include breakpoint(medium down) {
      @include flex-grid-column(12);
    }
  }

  &__form {
    @include flex-grid-column(6);
    position: relative;

    @include breakpoint(medium down) {
      @include flex-grid-column(12);
    }
  }

  &__field {
    margin-bottom: rem-calc(20)
  }

  &__textarea {
    min-height: rem-calc(160);
  }

  &__error {
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    transition: opacity 0.2s ease-in-out;

    &.is-active {
      opacity: 1;
      visibility: visible;
      z-index: 1;
    }
  }

  &__success {
    position: absolute;
    top: 0;
    left: 0;
    background: $palette-secondary;
    padding: 1.6rem;
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    transition: opacity 0.2s ease-in-out;

    &.is-active {
      opacity: 1;
      visibility: visible;
      z-index: 1;
    }
  }
}