// Components: Masonry Grid
// - - - - - - - - - - - - - - - - - - - - - - - - -

.masonry-grid {
  width: 100%;
  background-color: $palette-primary;
  color: $palette-tertiary;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  @include breakpoint(medium down) {
    flex-direction: column;
  }
}

.masonry-grid-item {
  max-width: calc(100% / 2);
  width: calc(100% / 2);
  display: flex;
  text-decoration: none;
  @include inView(800ms, 0);
  transition: all 300ms $global-transition;

  @include breakpoint(medium down) {
    max-width: 100%;
    width: 100%;

    &:nth-of-type(even) {
      background-color: $palette-secondary;
      color: $palette-primary;

      .masonry-grid-item__heading {
        color: $palette-primary;
      }

      .masonry-grid-item__icon {
        background-color: $palette-primary;

        &:before,
        &:after {
          background-color: $palette-primary;
        }        
      }
    }
  }

  &:nth-of-type(3),
  &:nth-of-type(4) {
    flex-direction: row-reverse;

    @include breakpoint(medium down) {
      flex-direction: row;
    }
  }

  @include hocus() {
    outline: none;
    background-color: $palette-accent;
    color: $palette-primary;

    img {
      transform: scale(1.1);
      filter: grayscale(0%);
    }

    .masonry-grid-item__heading {
      color: $palette-primary;
    }

    .masonry-grid-item__icon {
      width: 30px;
      background-color: $palette-primary;

      &:before,
      &:after {
        background-color: $palette-primary;
      }
    }
  }

  &__image {
    max-width: 50%;
    width: 50%;
    overflow: hidden;

    &.js-object-fit-fix {
      background-size: cover;
      background-position: center;

      img {
        display: none;
      }
    }

    @include breakpoint(medium down) {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      transition: all 300ms $global-transition;
      object-fit: cover;
      font-family: 'object-fit: cover;'; // IE11 Polyfill
      filter: grayscale(100%);
    }
  }

  &__content {
    max-width: 50%;
    width: 50%;
    padding: rem-calc(85 30 45);
    align-items: flex-end;
    flex-wrap: wrap;
    align-self: flex-end;

    @include breakpoint(medium down) {
      max-width: 100%;
      width: 100%;
      padding: rem-calc(20);
    }
  }

  &__heading {
    color: $palette-tertiary;
  }

  &__cta {
    font-size: rem-calc(12);
    font-family: $font-family-secondary;
    color: inherit;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 2px;
    position: relative;
    padding-right: rem-calc(40);
  }

  &__icon {
    @include arrow-right($palette-tertiary);
  }
}