// Components: Recent Articles
// - - - - - - - - - - - - - - - - - - - - - - - - -

.recent-articles {
  margin-bottom: $global-margin;

  &__row {
    @include flex-grid-row;
  }

  &__column {
    @include flex-grid-column;
  }

  &__heading {
    @include inView(800ms, 0);
    margin-bottom: rem-calc(50);
    display: inline-block;
  }

  &__all {
    @include btnLink($palette-primary);

    @include hocus() {
      outline: none;
      opacity: 0.5;

      .recent-articles__icon {
        width: 30px;
        right: 0;
      }
    }
  }

  &__icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: 18px;
    height: 1px;
    transition: 300ms ease;
    will-change: width;
    background-color: $palette-primary;

    &:before,
    &:after {
      content: '';
      position: absolute;
      height: 1px;
      width: 10px;
      background-color: $palette-primary;
    }

    &:before {
      top: -3px;
      right: -1px;
      transform: rotate(40deg);
    }

    &:after {
      top: 3px;
      right: -1px;
      transform: rotate(-40deg);
    }
  }
}