// Components: Video
// - - - - - - - - - - - - - - - - - - - - - - - - -

.video {
  @include inView(800ms, 0);
  margin-bottom: $global-margin;

  &__row {
    @include flex-grid-row;
  }

  &__col {
    @include flex-grid-column;
  }

  &__wrap {
    position: relative;
    padding-bottom: 56.25%;
  }

  iframe {
    width: 100%;
    height: 100%;
    border: none;
  }

  &__video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;    
  }
}

.video-banner {
  @include background-img;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  text-decoration: none;

  @include hocus() {
    outline: none;

    .video-banner__path {
      fill: $palette-paragraph;
      fill-opacity: 1;
      transition: all 400ms $global-transition;
    }
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($palette-primary, 0.1);
    z-index: index($z-stack, heroOverlay);    
  }

  &__action {
    @include absolute-center;
    display: block;
    z-index: index($z-stack, heroContent);
  }

  &__path {
    fill-opacity: 0.4;
    fill: $palette-tertiary;
    transition: all 0.2s ease-in-out;
  }
}