// - - - - - - - - - - - - - - - - - - - - - - - - - 
// Headings
// Style both the <h> tag and also a class
// - - - - - - - - - - - - - - - - - - - - - - - - -
h1,
.heading-1 {
  @include breakpoint(large up) {
    font-size: $font-size-heading-one;
  }

  @include breakpoint(small up) {
    font-size: $font-size-heading-three;
  }
}

h2,
.heading-2 {
  @include breakpoint(large up) {
    font-size: $font-size-heading-two;
  }

  @include breakpoint(small up) {
    font-size: rem-calc(30);
  }
}

h3,
.heading-3 {
  @include breakpoint(large up) {
    font-size: $font-size-heading-three;
  }

  @include breakpoint(small up) {
    font-size: 3.0rem;
  }
}

h4,
.heading-4 {
  @include breakpoint(large up) {
    font-size: $font-size-heading-four;
  }

  @include breakpoint(small up) {
    font-size: $font-size-heading-five;
  }
}

h5,
.heading-5 {
  @include breakpoint(small up) {
    font-size: $font-size-heading-five;
  }
}

h6,
.heading-6 {
  @include breakpoint(small up) {
    font-size: $font-size-primary;
  }
}

// - - - - - - - - - - - - - - - - - - - - - - - - - 
// Sub Headings
// Sizes of Sub Headings are inherited from the tag
// - - - - - - - - - - - - - - - - - - - - - - - - -
.sub-heading {
  font-family: $font-family-secondary;
  font-size: rem-calc(12);
  text-transform: uppercase;
  letter-spacing: 2px;

  &--primary {
    color: $palette-primary;
  }

  &--secondary {
    color: $palette-quaternary;
  }
}