// Component: Home Banner
// - - - - - - - - - - - - - - - - - - - - - - - - -

.home-banner {
  width: 100%; 
  height: 100vh;
  background-color: $palette-primary;
  display: flex;
  justify-content: center;
  align-items: center;
  color: $palette-tertiary;
  overflow: hidden;
  position: relative;

  @include breakpoint(small only) {
    align-items: flex-end;
    text-align: left !important;
    padding: rem-calc(20 20 80);
  }

  &__heading {
    color: $palette-secondary;
    margin-bottom: rem-calc(0);

    @include breakpoint(small only) {
      font-size: $font-size-heading-two;
    }
  }

  &__top-headline {
    display: inline-block;
    margin-bottom: rem-calc(30);
    position: relative;
    line-height: rem-calc(30);

    @include breakpoint(small only) {
      margin-bottom: rem-calc(10);
    }
  }

  &__tagline {
    font-size: rem-calc(18);
    display: inline-block;
    opacity: 0;
    transform: translateY(50%);
    animation: fadeInH2 0.5s linear 1.725s forwards;
    color: rgba($palette-tertiary, 0.7);
    margin-bottom: rem-calc(70);
    
    > span {
      margin-top: rem-calc(30);

      .guides > span:not(.margins) {
        display: none;
      }
    }

    @include breakpoint(small only) {
      margin-top: rem-calc(10);
      margin-bottom: rem-calc(10);
    }
  }

  &__headline {
    display: block;
    line-height: 6.1rem;
  }

  &__word {
    display: inline-block;
    position: relative;
    
    &:first-of-type {
      margin-right: rem-calc(30);
    }
  }

  &__inner {
    line-height: 6.1rem;
    display: block;

    @include breakpoint(small only) {
      line-height: 1.4;
    }
  }

  &__cta {
    @include horizontal-center;
    bottom: 85px;
    border: none;
    background: none;
    box-shadow: none;
    color: inherit;
    font-family: $font-family-secondary;
    text-transform: uppercase;
    font-size: rem-calc(12);
    letter-spacing: 2px;
    transition: all 400ms $global-transition;

    @include breakpoint(small only) {
      display: none;
    }

    @include hocus {
      outline: none;
      opacity: 0.5;
    }
  }

  &__guide {
    position: absolute;
    border: 0.1rem dashed $palette-tertiary;
    opacity: 0.1;   
    animation: drawHeight 1s linear 0.1s forwards;
    will-change: max-height; 

    @include breakpoint(small only) {
      display: none;
    }
  }

  &__guide-x {
    top: 50%;
    left: 0;
    width: 100vw;
    height: 0.1rem;
    transform: translateY(-50%);
  }

  &__guide-y {
    left: 50%;
    top: 0;
    height: 100vh;
    width: 0.1rem;
    border: 0.1rem dashed $palette-tertiary;
    transform: translateX(-50%);
  }

  &__dot {
    display: block;
    width: rem-calc(10);
    height: rem-calc(10);
    position: absolute;
    top: -20rem;
    left: 50%;
    transform: translateX(-50%);
    background: $palette-tertiary;
    border-radius: rem-calc(60);
    opacity: 0;
    will-change: top;
    animation: slideCircle 4s linear 2s infinite;
  }
}